
#mainpage {
  margin: 0.3rem;
  padding: 0.35rem;
  background-color: #FFFFff;
  border: solid #F0F0F0 1px;
  border-radius: 5px;
  color: #6a6f77;
}
#mainpage .input-box {
    /*margin-top: 10px;*/
}
#mainpage .input-box .input-box-item {
      margin-bottom: 0.16rem;
      height: 0.8rem;
      line-height: 0.8rem;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      font-size: 0.3rem;
}
#mainpage .input-box .input-box-item span {
        -ms-flex-preferred-size: 1.3rem;
            flex-basis: 1.3rem;
        text-align: center;
        color: #999999;
}
#mainpage .input-box .input-box-item .input-box-item-content {
        position: relative;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
        outline: none;
        -webkit-box-flex: 1;
            -ms-flex-positive: 1;
                flex-grow: 1;
        margin-left: 0.25rem;
        padding-left: 0.25rem;
        height: 0.8rem;
        border-radius: 4px;
        background-color: #f4f4f4;
        color: #4c4c4c;
}
#mainpage .input-box .input-box-item .input-box-item-content .today {
          float: right;
          padding-right: 0.2rem;
          color: #4c4c4c;
}
#mainpage .submit {
    margin-bottom: 12px;
    border-radius: 5px;
    height: 0.8rem;
    line-height: 0.8rem;
    background-color: #F28300;
    font-size: 0.3rem;
    color: #FFFFff;
    text-align: center;
}



