
.calendar .calendar-header {
  border-bottom: solid 1px #EEEEEE;
  background-color: #F28300;
  padding-top: 0.48rem;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.36rem;
  font-family: PingFang-SC-Bold;
  color: #FFFFFF;
}
.calendar .calendar-header .back {
    position: absolute;
    padding-left: 0.27rem;
    width: 1rem;
    font-size: 0.32rem;
}
.calendar .calendar-header .back .back-icon {
      font-family: iconfont;
}
.calendar .calendar-header .title {
    text-align: center;
}
.calendar .week {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #FFFFff;
  height: 0.5rem;
  line-height: 0.5rem;
}
.calendar .week .week-day {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    color: #111111;
    font-size: 0.24rem;
    text-align: center;
}
.calendar .week .weekend-day {
    color: #F28300;
}
.calendar .blank {
  margin-top: 1.28rem;
}
.calendar .calendar-content {
  position: absolute;
  top: 0.5rem;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  font-size: 0.29rem;
}
.calendar .calendar-content .calendar-content-item .calendar-content-title {
    width: 100%;
    text-align: center;
    height: 0.5rem;
    line-height: 0.5rem;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day {
    background: #ffffff;
    overflow: hidden;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day .holiday {
      color: #F28300;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day .day-item {
      position: relative;
      float: left;
      width: 14.2%;
      height: 1.27rem;
      text-align: center;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day .day-item .day-item-num {
        margin: 0.42rem auto 0;
        height: 0.58rem;
        width: 0.58rem;
        line-height: 0.58rem;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day .day-item .CheckInDate, .calendar .calendar-content .calendar-content-item .calendar-content-day .day-item .CheckOutDate {
        border-radius: 0.14rem;
        color: #ffffff;
        background-color: #F28300;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day .day-item .check-block {
        display: block;
        height: 0.27rem;
        line-height: 0.27rem;
        font-size: 0.2rem;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day .day-item .restDay {
        position: absolute;
        top: 0.2rem;
        right: 0;
        font-size: 0.18rem;
        color: #F28300;
}
.calendar .calendar-content .calendar-content-item .calendar-content-day .disable {
      color: gray;
}



